.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.full-container {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: row;

  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.primary-container {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

td {
  padding: 5px 200px 5px 10px!important;
}

.fitts-display {
  margin-left: 100px;
  margin-top: 50px;
  max-height: 500px;
  min-width: 300px;
  max-width: 600px;
  overflow-y: scroll;
  display: flex;
}

.scrollable {
  max-height: 300px;
  overflow-y: scroll;
  display: block;
}

.centerWithinMe {
  text-align: center;
  align-items: center;
}

.board {
  background-color: whitesmoke;
}

.stylish-heading {
  border: 5px solid peachpuff;
  padding: 3px;
  background-color: whitesmoke;
  color: black;
}

.tile {
  width: 100px;
  height: 100px;
  text-align: center;
}

.board > .row {
  margin-bottom: 0px !important;
}

.board > .row > .col {
  padding: 0 0rem !important;
}


.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


.desc {
  max-width: 1100px;
  font-size: 14px;
}